import React, {useState, useEffect} from "react";
import {Container, AppBar, Typography, Grow, Grid} from '@material-ui/core';
import { useDispatch } from "react-redux";


import Posts from './components/Posts/Posts';
import Form from './components/Form/Form';
import {getPosts} from './actions/posts';

//import picture of heroes we downloaded in images file and assign it to variables
import logo from './images/logo.png';
import damage from './images/damage.png';
import lucio from './images/lucio.png';
import lucio1 from './images/lucio1.png';
import moira from './images/moira.png';
import mcree from './images/mcree.png';
import soldier76 from './images/76.png';

//get style format
import myStyle from './styles';


const App = () => {
    const [currentId, setCurrentId] = useState(null);

    const myClass= myStyle();
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(getPosts());
    }, [currentId, dispatch]);

    return(
       <Container maxWidth= "lg">
        <AppBar className={myClass.appBar} position="static" color="inherit">
                
                
                <img className={myClass.image} src={logo} alt="logo" height="60" />
                <Typography className={myClass.heading} variant="h2" align="center">
                    <b><i>verwatch Scores</i></b>
                </Typography>
                <img className={myClass.image} src={lucio} alt="sigma" height="60" />
                <img className={myClass.image} src={lucio1} alt="lucio" height="60" />
                <img className={myClass.image} src={moira} alt="moira" height="60" />
                <img className={myClass.image} src={mcree} alt="mcree" height="60" />
                <img className={myClass.image} src={soldier76} alt="soldier76" height="60" />

                
            </AppBar>

            <Grow in>
                <Container>
                    <Grid container justifyContent="space-between" alignItems="stretch" spacing={3}>
                        <Grid item xs={12} sm={7}>
                            <Posts setCurrentId={setCurrentId}/>
                        </Grid>

                        <Grid item xs={12} sm={4}>
                            <Form currentId={currentId} setCurrentId={setCurrentId}/>
                        </Grid>
                    </Grid>
                </Container>
            </Grow>

            
       </Container>
    )
}

export default App;